export default {
  props: {
    theme: {
      type: [String, Array],
      default: ""
    },
    baseClassName: {
      type: String,
      default: "component"
    }
  },

  computed: {
    themeClasses() {
      const generateClassName = str => `${this.baseClassName}_style_${str}`;

      if (Array.isArray(this.theme)) {
        return this.theme.map(generateClassName);
      }

      return this.theme
        .trim()
        .split(" ")
        .filter(t => t !== "")
        .map(generateClassName);
    }
  }
};



