import Transformer from "./Transformer";

export default class SettingsTransformer extends Transformer {
  constructor(settings = {}) {
    super();
    this.settings = settings;
  }

  getCutTypes() {
    return this.settings.cutTypes || {};
  }

  getCutMaterials() {
    return this.settings.cutMaterials || {};
  }
}



