<template>
  <span :class="classes">
    <select
      :class="[
        'form-control',
        'form-control_type_select',
        `${baseClassName}__control`
      ]"
      :id="uniqId"
      :name="name"
      :value="mutableValue"
      :multiple="multiple"
      :disabled="disabled"
      @change="onChange"
    >
      <option
        v-if="placeholder"
        :class="`${baseClassName}__placeholder`"
        value=""
        :selected="mutableValue === '' ? 'selected' : false"
        disabled
      >
        {{ placeholder }}
      </option>
      <option
        v-if="hasOptions"
        v-for="option in options"
        :key="option.value"
        :value="option.value"
        :label="option.label"
        :disabled="option.disabled"
        :selected="
          option.value.toString() === mutableValue.toString()
          ? 'selected'
          : false
        "
        :hidden="option.hidden"
      >
        {{ option.label }}
      </option>
      <slot v-if="!hasOptions"></slot>
    </select>
  </span>
</template>

<script>
import ThemeClassesMixin from "@/mixins/themeClasses";
import UniqIdMixin from "@/mixins/uniqId";

export default {
  name: "Select",

  mixins: [ThemeClassesMixin, UniqIdMixin],

  props: {
    value: {
      default: ""
    },
    name: String,
    theme: {
      type: String,
      default: "default"
    },
    options: {
      type: Array,
      default() {
        return [];
      }
    },
    placeholder: String,
    disabled: Boolean,
    multiple: {
      type: Boolean,
      default: false
    },
    block: Boolean,
    baseClassName: {
      type: String,
      default: "select"
    },
    resetOnOptionsChange: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      mutableValue: this.value,
      mutableOptions: []
    };
  },

  computed: {
    classes() {
      return [
        this.baseClassName,
        {
          [`${this.baseClassName}_type_${this.type}`]: this.type,
          [`${this.baseClassName}_block`]: this.block,
          [`${this.baseClassName}_inline`]: !this.block
        },
        ...this.themeClasses
      ];
    },
    hasOptions() {
      return this.options.length > 0;
    }
  },

  watch: {
    value(newVal) {
      this.mutableValue = newVal;
    },
    mutableValue() {
      this.$emit("input", this.mutableValue);
    },
    options(newVal) {
      this.mutableOptions = newVal;
    },

    mutableOptions() {
      if (this.resetOnOptionsChange) {
        this.mutableValue = this.multiple ? [] : "";
      }
    }
  },

  methods: {
    getSelectedValue(options) {
      return (
        options
          .filter(option => option.selected)
          // eslint-disable-next-line dot-notation
          .map(option => ("_value" in option ? option["_value"] : option.value))
      );
    },
    onChange({ target }) {
      const options = [].slice.call(target.options);
      const selectedVal = this.getSelectedValue(options);

      this.mutableValue = target.multiple ? selectedVal : selectedVal[0];

      this.$nextTick(() => {
        this.$emit("change", this.mutableValue);
      });
    }
  }
};
</script>



