<template>
  <div class="rectangle">
    <div class="form__item input-group">
      <FormItem class="input-group__item"
        label="Длина:"
        :errors="validation['rectangle.width']"
        htmlFor="rectangle.width"
      >
        <Input
          v-model="width"
          id="rectangle.width"
          name="width"
          placeholder="Длина"
          type="number"
          :min="limits.width.min"
          :max="limits.width.max"
          icon="form_width"
          step="0.1"
          block
        />
      </FormItem>

      <FormItem class="input-group__item"
        label="Ширина:"
        :errors="validation['rectangle.height']"
        htmlFor="rectangle.height"
      >
        <Input
          v-model="height"
          id="rectangle.height"
          name="height"
          placeholder="Ширина"
          type="number"
          :min="limits.height.min"
          :max="limits.height.max"
          icon="form_height"
          step="0.1"
          block
        />
      </FormItem>
    </div>

    <FormItem class="form__item"
      label="Радиус углов:"
      :errors="validation['rectangle.radius']"
      htmlFor="rectangle.radius"
    >
      <Input
        v-model="radius"
        id="rectangle.radius"
        name="radius"
        placeholder="Радиус углов"
        type="number"
        :min="0"
        :max="radiusMax"
        icon="form_corner-radius"
        block
      />
    </FormItem>
  </div>
</template>


<script>
import { createHelpers } from "vuex-map-fields";

import { Input, FormItem } from "@/components";
import { mapGetters, mapState } from "vuex";

const { mapFields } = createHelpers({
  getterType: "shape/getField",
  mutationType: "shape/updateField"
});

export default {
  name: "ConstructorRectangle",

  components: {
    Input,
    FormItem
  },

  computed: {
    ...mapFields({
      width: "rectangle.width",
      height: "rectangle.height",
      radius: "rectangle.radius"
    }),
    ...mapGetters({
      limits: "materialLimits"
    }),
    ...mapState({
      validation: state => state.errors.validation
    }),
    radiusMax() {
      return Math.ceil(this.width / 2);
    }
  }
};
</script>



