<template>
  <label :class="classes" :for="uniqId">
    <input
      :class="[
        'form-control',
        'form-control_type_checkbox',
        `${this.baseClassName}__control`,
        className
      ]"
      type="checkbox"
      :id="uniqId"
      :name="name"
      :value="value"
      :required="required"
      :disabled="disabled"
      @change="onChange"
      :checked="state"
    />
    <span :class="`${this.baseClassName}__box`" v-if="boxHelper"></span>
    <span v-if="label || hasSlot()"
      :class="[
        `${this.baseClassName}__label`,
        { 'visuallyhidden' :labelHidden }
      ]">
      {{ label }}
      <slot></slot>
    </span>
  </label>
</template>

<script>
import HasSlotMixin from "@/mixins/hasSlot";
import UniqIdMixin from "@/mixins/uniqId";
import ThemeClassesMixin from "@/mixins/themeClasses";

export default {
  name: "Checkbox",

  mixins: [HasSlotMixin, UniqIdMixin, ThemeClassesMixin],

  model: {
    prop: "model",
    event: "change"
  },

  props: {
    label: String,
    model: {
      type: [String, Array],
      default: undefined
    },
    name: {
      type: String,
      default: null
    },
    value: {
      type: [String, Boolean],
      default: ""
    },
    className: {
      type: String,
      default: ""
    },
    theme: {
      type: String,
      default: "default"
    },
    required: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    labelHidden: {
      type: Boolean,
      default: false
    },
    boxHelper: {
      type: Boolean,
      default: true
    },
    baseClassName: {
      type: String,
      default: "checkbox"
    }
  },

  computed: {
    classes() {
      return [this.baseClassName, ...this.themeClasses];
    },
    state() {
      if (this.model === undefined) {
        return this.checked;
      }

      if (Array.isArray(this.model)) {
        return this.model.indexOf(this.value) !== -1;
      }

      return this.model;
    }
  },

  methods: {
    onChange() {
      this.toggle();
    },
    toggle() {
      if (this.disabled) {
        return;
      }

      let value = this.model;
      if (Array.isArray(value)) {
        value = value.slice();
        const i = value.indexOf(this.value);
        if (i === -1) {
          value.push(this.value);
        } else {
          value.splice(i, 1);
        }
      } else {
        value = !this.state;
      }

      this.$emit("change", value);
    }
  }
};
</script>



