<template>
  <label :class="classes" :for="uniqId">
    <input
      :class="[
        'form-control',
        'form-control_type_radio',
        `${this.baseClassName}__control`,
        className
      ]"
      type="radio"
      :id="uniqId"
      :name="name"
      :value="value"
      :required="required"
      :disabled="disabled"
      @change="onChange"
      :checked="state"
    />
    <span :class="`${this.baseClassName}__box`" v-if="boxHelper"></span>
    <span v-if="label || hasSlot()"
      :class="[
        `${this.baseClassName}__label`,
        { 'visuallyhidden' :labelHidden }
      ]">
      <template v-if="!hasSlot()">{{ label }}</template>
      <slot></slot>
    </span>
  </label>
</template>

<script>
import HasSlotMixin from "@/mixins/hasSlot";
import UniqIdMixin from "@/mixins/uniqId";
import ThemeClassesMixin from "@/mixins/themeClasses";

export default {
  name: "Radio",

  mixins: [HasSlotMixin, UniqIdMixin, ThemeClassesMixin],

  model: {
    prop: "model",
    event: "change"
  },

  props: {
    label: {
      type: String,
      default: ""
    },
    model: {
      type: String,
      default: undefined
    },
    name: {
      type: String,
      default: null
    },
    value: {
      type: String,
      default: ""
    },
    className: {
      type: String,
      default: ""
    },
    theme: {
      type: String,
      default: "default"
    },
    required: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    labelHidden: {
      type: Boolean,
      default: false
    },
    boxHelper: {
      type: Boolean,
      default: true
    },
    baseClassName: {
      type: String,
      default: "radio"
    }
  },

  computed: {
    classes() {
      return [this.baseClassName, ...this.themeClasses];
    },
    state() {
      if (this.model === undefined) {
        return this.checked;
      }

      return this.model === this.value;
    }
  },

  methods: {
    onChange() {
      this.toggle();
    },
    toggle() {
      if (this.disabled) {
        return;
      }

      this.$emit("change", this.state ? "" : this.value);
    }
  }
};
</script>



