<template>
  <ConstructorWrapper />
</template>

<script>
import { mapActions } from "vuex";
import { RESET_SHAPE } from "@/store/action-types";
import ConstructorWrapper from "@/containers/Constructor/Wrapper";

export default {
  name: "ConstructorNew",

  components: {
    ConstructorWrapper
  },

  created() {
    this.resetShape();
  },

  methods: {
    ...mapActions({
      resetShape: `shape/${RESET_SHAPE}`
    })
  }
};
</script>



