
<template>
  <div class="constructor-wrapper">
    <div class="constructor-wrapper__section">
      <div class="constructor-wrapper__form">

        <div class="constructor-wrapper__col">
          <div class="constructor-wrapper__col-wrapper">
            <h2 class="constructor-wrapper__col-title">Конфигураци детали:</h2>

            <FormItem class="form__item"
              label="Название детали:"
              :errors="validation.title"
              htmlFor="shape.title"
            >
              <Input
                v-model="title"
                id="shape.title"
                name="title"
                placeholder="Название детали"
                block
              />
            </FormItem>

             <FormItem class="form__item"
              label="Тип резки:"
              :errors="validation.cutType"
              htmlFor="shape.cutType"
            >
              <Fieldset>
                <Select
                  :options="cutTypesOptions"
                  placeholder="Выберите тип резки"
                  v-model="cutType"
                  id="shape.cutType"
                  name="cutType"
                  block
                />
              </Fieldset>
            </FormItem>

            <FormItem class="form__item"
              label="Форма детали:"
              :errors="validation.type"
              htmlFor="shape.type"
            >
              <Fieldset>
                <Select
                  :options='[
                    { label: "Квадрат/Прямоугольник", value: "rectangle" },
                    { label: "Круг", value: "circle" },
                    { label: "Треугольник", value: "triangle" }
                  ]'
                  placeholder="Выберите форму"
                  v-model="shapeType"
                  id="shape.type"
                  name="type"
                  @change="onShapeTypeChange"
                  block
                />
              </Fieldset>
            </FormItem>

            <Rectangle class="form__item" v-if="isRectangle" />
            <ConstructorCircle class="form__item" v-if="isCircle" />
            <Triangle class="form__item" v-if="isTriangle" />

            <FormItem class="form__item"
              label="Материал детали:"
              :errors="validation.material"
              htmlFor="shape.material"
            >
              <Select
                v-model="material"
                id="shape.material"
                name="material"
                :options="cutMaterialsOptions"
                placeholder="Выберите материал"
                :disabled="cutMaterialsOptions.length <= 0"
                :resetOnOptionsChange="true"
                block
              />
            </FormItem>

            <FormItem class="form__item"
              label="Количество деталей:"
              :errors="validation.quantity"
              htmlFor="shape.quantity"
            >
              <Input
                v-model="quantity"
                id="shape.quantity"
                name="quantity"
                type="number"
                placeholder="Количество деталей"
                :min="1"
                icon="form_quantity"
                block
              />
            </FormItem>
          </div>
        </div>

        <div class="constructor-wrapper__col">
          <div class="constructor-wrapper__draft">
            <Draw />
          </div>
          <div class="constructor-wrapper__draft-controls">
            <div class="constructor-wrapper__draft-controls-item">
              <Button
                @click="caclulate"
                color="blue"
                size="large"
                theme="gradient solid rounded bold uppercase letter-spacing"
                icon
              >
                <Icon name="calculator" />
                <span>Рассчитать стоимость</span>
              </Button>
            </div>
            <div class="constructor-wrapper__draft-controls-item">
              <Button
                @click="save"
                :disabled="isSend"
                color="blue"
                theme="transparent solid rounded"
                icon
              >
                <Icon name="save" />
                <span>Сохранить деталь</span>
              </Button>
            </div>
          </div>
        </div>

        <div class="constructor-wrapper__col" v-if="isHoleEditVisible">
          <HoleForm :id="holeEditParams.id" />
        </div>

        <div class="constructor-wrapper__col" v-else>
          <div class="constructor-wrapper__col-wrapper">
            <h2 class="constructor-wrapper__col-title">Отверстия:</h2>
          </div>
          <HolesList />
        </div>

      </div>
    </div>

    <div class="constructor-wrapper__section" id="results">
      <div class="constructor-wrapper__results" v-if="cost">
        <h2 class="section-title">Результат рассчета:</h2>
        <Results :data="cost" />
      </div>
    </div>

    <RemoveHoleModal />
    <ChShapeTypeModal
      :onSuccess="onSuccessChangeShape"
      :onCancel="onCancelChangeShape"
      :onClose="onCloseChangeShape"
    />
    <ModifyShapeModal
      :onSuccess="onSuccessModifyShape"
      :onCancel="onCloseModifyShape"
      :onClose="onCloseModifyShape"
      :shapeName="title"
    />

  </div>
</template>

<script>
import { mapGetters, mapState, mapActions, mapMutations } from "vuex";
import { createHelpers } from "vuex-map-fields";
import { POP, PUSH } from "vuex-modal";

import { Button, Icon, FormItem, Fieldset, Input, Select } from "@/components";

import Rectangle from "@/containers/Constructor/Rectangle";
import ConstructorCircle from "@/containers/Constructor/Circle";
import Triangle from "@/containers/Constructor/Triangle";
import HolesList from "@/containers/Constructor/HolesList";
import Results from "@/containers/Constructor/Results";
import HoleForm from "@/containers/Constructor/HoleForm";
import RemoveHoleModal from "@/containers/Constructor/RemoveHoleModal";
import ChShapeTypeModal from "@/containers/Constructor/ChangeShapeTypeModal";
import ModifyShapeModal from "@/containers/Constructor/ModifyShapeModal";
import Draw from "@/containers/Draw/Draw";

import {
  CALCULATE_COST,
  SAVE_SHAPE,
  UPDATE_SHAPE,
  HOLE_EDIT_CLOSE
} from "@/store/action-types";
import { REMOVE_ALL_HOLES } from "@/store/mutation-types";
import CutTypeTransformer from "@/transformers/CutTypeTransformer";
import CutMaterialTransformer from "@/transformers/CutMaterialTransformer";

const { mapFields } = createHelpers({
  getterType: "shape/getField",
  mutationType: "shape/updateField"
});

export default {
  name: "ConsturctorWrapper",

  components: {
    Rectangle,
    ConstructorCircle,
    Triangle,
    HolesList,
    Results,
    HoleForm,
    RemoveHoleModal,
    ChShapeTypeModal,
    ModifyShapeModal,
    Draw,
    Button,
    Icon,
    FormItem,
    Fieldset,
    Input,
    Select
  },

  data() {
    return {
      isSend: false,
      cost: null,
      shapeType: this.type,
      shapeId: null
    };
  },

  computed: {
    ...mapFields(["type", "title", "quantity", "cutType", "material"]),
    ...mapState({
      isHoleEditVisible: state => state.holeEdit.visible,
      holeEditParams: state => state.holeEdit.params,
      shape: state => state.shape,
      cutTypesData: state => state.settings.cutTypes,
      cutMaterialsData: state => state.settings.cutMaterials,
      validation: state => state.errors.validation
    }),
    ...mapGetters({
      isRectangle: "shape/isRectangle",
      isCircle: "shape/isCircle",
      isTriangle: "shape/isTriangle",
      isHaveHoles: "shape/isHaveHoles"
    }),
    cutTypes() {
      return new CutTypeTransformer(this.cutTypesData);
    },
    cutMaterials() {
      return new CutMaterialTransformer(this.cutMaterialsData);
    },
    cutTypesOptions() {
      return this.cutTypes.getOptionsList();
    },
    cutMaterialsIds() {
      return this.cutType ? this.cutTypes.getCutMaterialsIds(this.cutType) : [];
    },
    cutMaterialsOptions() {
      return this.cutType
        ? this.cutMaterials.getOptionsList(this.cutMaterialsIds)
        : [];
    }
  },

  methods: {
    ...mapMutations({
      removeAllHoles: `shape/${REMOVE_ALL_HOLES}`
    }),
    ...mapActions({
      saveShapeAction: `shape/${SAVE_SHAPE}`,
      updateShapeAction: `shape/${UPDATE_SHAPE}`,
      calculateCostAction: `shape/${CALCULATE_COST}`,
      closeModal: POP,
      openModal: PUSH,
      holeEditClose: `holeEdit/${HOLE_EDIT_CLOSE}`
    }),
    save() {
      const { params, name } = this.$route;
      this.isSend = true;

      switch (name) {
        case "constructor/order":
        case "constructor/new":
          this.saveShapeAction().then(() => {
            this.isSend = false;
          });
          break;

        case "constructor/edit":
          if (params.id) {
            this.openModal({ name: "modifyShape" });
            this.shapeId = params.id;
          }
          break;

        default:
          this.isSend = false;
          break;
      }
    },
    caclulate() {
      this.calculateCostAction().then(data => {
        if (data) {
          this.cost = data.cost ? data.cost : null;
          this.$scrollTo("#results");
        }
      });
    },
    onShapeTypeChange() {
      if (this.isHaveHoles) {
        this.openModal({ name: "changeShape" });
      } else {
        this.type = this.shapeType;
      }
    },
    onCancelChangeShape() {
      this.closeModal({ name: "changeShape" });
      this.type = this.shapeType;
    },
    onSuccessChangeShape() {
      this.removeAllHoles();
      this.holeEditClose();
      this.closeModal({ name: "changeShape" });
      this.type = this.shapeType;
    },
    onCloseChangeShape() {
      this.closeModal({ name: "changeShape" });
      this.shapeType = this.type;
    },
    onSuccessModifyShape() {
      this.updateShapeAction(this.shapeId).then(() => {
        this.isSend = false;
        this.closeModal({ name: "modifyShape" });
      });
    },
    onCloseModifyShape() {
      this.isSend = false;
      this.closeModal({ name: "modifyShape" });
    }
  }
};
</script>



