<script>
import ThemeClassesMixin from "@/mixins/themeClasses";

export default {
  name: "Button",

  mixins: [ThemeClassesMixin],

  props: {
    type: {
      type: String,
      default: "button"
    },
    color: String,
    to: {
      type: [String, Object],
      default: null
    },
    size: {
      type: String,
      default: "medium"
    },
    theme: {
      type: [String, Array],
      default: ""
    },
    block: Boolean,
    disabled: {
      type: Boolean,
      default: false
    },
    icon: Boolean,
    value: null,
    baseClassName: {
      type: String,
      default: "button"
    }
  },

  computed: {
    classes() {
      const classes = [
        this.baseClassName,
        {
          [`${this.baseClassName}_color_${this.color}`]: this.color,
          [`${this.baseClassName}_size_${this.size}`]: this.size,
          [`${this.baseClassName}_block`]: this.block,
          [`${this.baseClassName}_disabled`]: this.disabled,
          [`${this.baseClassName}_icon`]: this.icon
        },
        ...this.themeClasses
      ];

      return classes;
    }
  },

  methods: {
    handleClick(event) {
      this.$emit("click", event);
    }
  },

  // eslint-disable-next-line no-unused-vars
  render(h) {
    const ButtonTag = this.to ? "a" : "button";
    const type = this.to ? null : this.type;

    return (
      <ButtonTag
        onClick={this.handleClick}
        class={this.classes}
        type={type}
        href={this.to}
        disabled={this.to ? false : this.disabled}
      >
        {this.$slots.default}
      </ButtonTag>
    );
  }
};
</script>



