<template>
  <Modal name="modifyShape" :disable-backdrop="true">
    <Dialog
      name="modifyShape"
      title="Перезаписать деталь?"
      :close="onClose"
    >
      <p>
        Деталь «{{ shapeName }}» будет отредактирована.
        <br>
        Перезаписать деталь?
      </p>

      <template slot="footer">
        <Button
          color="gray"
          size="m"
          theme="solid rounded"
          @click="onCancel"
        >
          Отмена
        </Button>
        &nbsp;
        <Button
          color="orange"
          size="m"
          theme="solid rounded"
          @click="onSuccess"
        >
          Перезаписать
        </Button>
      </template>
    </Dialog>
  </Modal>
</template>

<script>
import { Modal } from "vuex-modal";
import { Button, Dialog } from "@/components";

export default {
  name: "ConstructorModifyShapeModal",

  components: {
    Modal,
    Dialog,
    Button
  },

  props: {
    onSuccess: {
      type: Function,
      default: () => {}
    },
    onCancel: {
      type: Function,
      default: () => {}
    },
    onClose: {
      type: Function,
      default: () => {}
    },
    shapeName: {
      type: String,
      default: ""
    }
  }
};
</script>



