<template>
  <span :class="classes">
    <span v-if="icon" :class="`${baseClassName}__icon`">
      <Icon :name="icon" />
    </span>
    <input
      :class="[
        'form-control',
        'form-control_type_input',
        `${baseClassName}__control`
      ]"
      :id="uniqId"
      :name="name"
      :type="type"
      :value="value"
      :min="min"
      :max="max"
      :step="step"
      :placeholder="placeholder"
      @input="onInput"
    >
  </span>
</template>

<script>
import { Icon } from "@/components";

import ThemeClassesMixin from "@/mixins/themeClasses";
import UniqIdMixin from "@/mixins/uniqId";

export default {
  name: "Input",

  components: {
    Icon
  },

  mixins: [ThemeClassesMixin, UniqIdMixin],

  props: {
    value: {
      type: [String, Number],
      default: ""
    },
    name: String,
    placeholder: String,
    type: {
      type: String,
      default: "text"
    },
    theme: {
      type: [String, Array],
      default: "default"
    },
    block: Boolean,
    icon: String,
    min: Number,
    max: Number,
    step: String,
    baseClassName: {
      type: String,
      default: "input"
    }
  },

  computed: {
    classes() {
      return [
        this.baseClassName,
        {
          [`${this.baseClassName}_type_${this.type}`]: this.type,
          [`${this.baseClassName}_icon`]: this.icon,
          [`${this.baseClassName}_block`]: this.block
        },
        ...this.themeClasses
      ];
    }
  },

  methods: {
    onInput(event) {
      this.$emit("input", event.target.value);
    }
  }
};
</script>



