<template>
  <div class="svg-pan-zoom grabbable" ref="svgPanZoomContainer">
    <slot />
    <Button
      color="gray"
      theme="solid"
      class="svg-pan-zoom__reset"
      @click="handleReset"
      title="Центрировать деталь"
    >
      <Icon name="reset" title="Центрировать деталь" />
    </Button>
  </div>
</template>

<script>
import { Button, Icon } from "@/components";
import PanZoom from "./pan-zoom";

export default {
  name: "SVGPanZoom",

  components: {
    Button,
    Icon
  },

  data() {
    return {
      panZoom: null
    };
  },

  watch: {
    $route(to, from) {
      if (to.params.id && from.params.id !== to.params.id) {
        this.initPanZoom();
      }
    }
  },

  mounted() {
    this.$nextTick(() => {
      this.initPanZoom();
    });
  },

  updated() {
    this.initPanZoom();
  },

  methods: {
    initPanZoom() {
      const svg = this.$slots.default[0].elm;
      const container = this.$refs.svgPanZoomContainer;

      if (svg) {
        this.panZoom = new PanZoom(svg, {
          eventArea: container
        });
      }
    },

    handleReset() {
      this.panZoom.reset();
    }
  }
};
</script>

<style lang="stylus">
.svg-pan-zoom
  width 100%
  height 100%
  position relative

  > svg
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    transform-origin 0 0
    transform translateZ(0)
    padding 40px
    box-sizing border-box

  &__reset
    position absolute
    right 20px
    top 20px
    z-index 100
    width 40px
    height 40px
    border-radius 100px
    padding 5px
    box-shadow 0 0 8px 0 rgba(0, 26, 51, 0.2)
    text-align center
    color #536e88 !important
    aligin-items center
    align-content center
</style>



