function getXY(obj) {
  return {
    x: obj.pageX,
    y: obj.pageY
  };
}

function touchDistance(touch1, touch2) {
  const dx = Math.abs(touch2.x - touch1.x);
  const dy = Math.abs(touch2.y - touch1.y);
  return Math.sqrt(dx * dx + dy * dy);
}

function getMidpoint(point1, point2) {
  return {
    x: (point1.x + point2.x) / 2,
    y: (point1.y + point2.y) / 2
  };
}

function getPoints(event) {
  if (event.touches) {
    return Array.from(event.touches).map(t => getXY(t));
  }

  return [getXY(event)];
}

export default class PanZoom {
  constructor(
    target,
    { eventArea = target, shouldCaptureFunc = () => true } = {}
  ) {
    this.target = target;
    this.shouldCaptureFunc = shouldCaptureFunc;
    this.dx = 0;
    this.dy = 0;
    this.scale = 1;
    this.active = 0;
    this.lastPoints = [];

    // bind
    ["onPointerDown", "onPointerMove", "onPointerUp"].forEach(funcName => {
      this[funcName] = this[funcName].bind(this);
    });

    // bound events
    eventArea.addEventListener("mousedown", this.onPointerDown);
    eventArea.addEventListener("touchstart", this.onPointerDown);

    // unbonud
    eventArea.addEventListener("wheel", event => this.onWheel(event));
  }

  reset() {
    this.dx = 0;
    this.dy = 0;
    this.scale = 1;
    this.update();
  }

  onWheel(event) {
    if (!this.shouldCaptureFunc(event.target)) return;
    event.preventDefault();

    const boundingRect = this.target.getBoundingClientRect();
    let delta = event.deltaY;

    if (event.deltaMode === 1) {
      // 1 is "lines", 0 is "pixels"
      // Firefox uses "lines" when mouse is connected
      delta *= 15;
    }

    // stop mouse wheel producing huge values
    delta = Math.max(Math.min(delta, 60), -60);

    const scaleDiff = delta / 300 + 1;

    // avoid to-small values
    if (this.scale * scaleDiff < 0.05) return;

    this.scale *= scaleDiff;
    this.dx -= (event.pageX - boundingRect.left) * (scaleDiff - 1);
    this.dy -= (event.pageY - boundingRect.top) * (scaleDiff - 1);
    this.update();
  }

  onFirstPointerDown() {
    document.addEventListener("mousemove", this.onPointerMove);
    document.addEventListener("mouseup", this.onPointerUp);
    document.addEventListener("touchmove", this.onPointerMove);
    document.addEventListener("touchend", this.onPointerUp);
  }

  onPointerDown(event) {
    if (event.type === "mousedown" && event.which !== 1) return;
    if (!this.shouldCaptureFunc(event.target)) return;
    event.preventDefault();

    this.lastPoints = getPoints(event);
    this.active = this.active + 1;

    if (this.active === 1) {
      this.onFirstPointerDown(event);
    }
  }

  onPointerMove(event) {
    event.preventDefault();
    const points = getPoints(event);
    const averagePoint = points.reduce(getMidpoint);
    const averageLastPoint = this.lastPoints.reduce(getMidpoint);
    const boundingRect = this.target.getBoundingClientRect();

    this.dx += averagePoint.x - averageLastPoint.x;
    this.dy += averagePoint.y - averageLastPoint.y;

    if (points[1]) {
      const scaleDiff =
        touchDistance(points[0], points[1]) /
        touchDistance(this.lastPoints[0], this.lastPoints[1]);
      this.scale *= scaleDiff;
      this.dx -= (averagePoint.x - boundingRect.left) * (scaleDiff - 1);
      this.dy -= (averagePoint.y - boundingRect.top) * (scaleDiff - 1);
    }

    this.update();
    this.lastPoints = points;
  }

  update() {
    const { dx, dy, scale } = this;
    const translate = `translate3d(${dx}px, ${dy}px, 0) scale(${scale})`;
    this.target.style.WebkitTransform = translate;
    this.target.style.transform = translate;
  }

  onPointerUp(event) {
    event.preventDefault();
    this.active = this.active - 1;
    this.lastPoints.pop();

    if (this.active) {
      this.lastPoints = getPoints(event);
      return;
    }

    document.removeEventListener("mousemove", this.onPointerMove);
    document.removeEventListener("mouseup", this.onPointerUp);
    document.removeEventListener("touchmove", this.onPointerMove);
    document.removeEventListener("touchend", this.onPointerUp);
  }
}



