
<template>
  <div :class="classes">
    <div v-if="label" :class="`${baseClassName}__label`">
      <label v-if="htmlFor" :for="htmlFor" class="label" v-html="label" />
      <span v-else class="label" v-html="label" />
    </div>

    <div :class="`${baseClassName}__control`">
      <slot></slot>
    </div>

    <ul v-if="errors.length > 0" :class="`${baseClassName}__errors`">
      <li v-for="(error, index) in errors" :key="index">{{ error.message }}</li>
    </ul>
  </div>
</template>

<script>
import types from "@/helpers/propTypes";

import ThemeClassesMixin from "@/mixins/themeClasses";

export default {
  name: "FormItem",

  mixins: [ThemeClassesMixin],

  props: {
    label: String,
    labelPosition: {
      validator: types.enum(["top", "left", "right"]),
      default: "top"
    },
    htmlFor: String,
    errors: {
      type: Array,
      default: () => []
    },
    baseClassName: {
      type: String,
      default: "form-item"
    }
  },

  computed: {
    classes() {
      const classes = [
        this.baseClassName,
        {
          [`${this.baseClassName}_label_${this.labelPosition}`]: this
            .labelPosition
        },
        ...this.themeClasses
      ];

      return classes;
    }
  }
};
</script>



