<template>
  <div
    :class="classes"
    :id="name"
    role="dialog"
    aria-modal="true"
    :aria-labelledby="`${name}-dialog__title`"
  >
    <header class="dialog__header" v-if="(title || close)">
      <h2
        :class="`${baseClassName}__title`"
        :id="`${name}-dialog__title`"
      >
        {{ title }}
      </h2>
      <Button
        :class="`${baseClassName}__close`"
        v-if="close"
        @click="close"
      >
        <Icon name="close" />
        <span class="visuallyhidden">Close this dialog window</span>
      </Button>
    </header>

    <main :class="`${baseClassName}__content`">
      <slot></slot>
    </main>

    <footer :class="`${baseClassName}__footer`">
      <slot name="footer"></slot>
    </footer>
  </div>
</template>

<script>
import { Modal } from "vuex-modal";
import { Button, Icon } from "@/components";

import ThemeClassesMixin from "@/mixins/themeClasses";

export default {
  name: "Dialog",

  components: {
    Button,
    Icon,
    Modal
  },

  mixins: [ThemeClassesMixin],

  props: {
    name: {
      type: String,
      default: "dialog"
    },
    theme: String,
    title: String,
    close: Function,
    baseClassName: {
      type: String,
      default: "dialog"
    }
  },

  computed: {
    classes() {
      /* eslint-disable indent */
      const themes = this.theme
        ? this.theme
            .split(" ")
            .filter(t => t !== "")
            .map(t => `${this.baseClassName}_theme_${t}`)
        : [];
      /* eslint-enable indent */

      return [this.baseClassName, ...themes];
    }
  }
};
</script>



