import uid from "@/helpers/generateId";

export default {
  props: {
    id: {
      type: String,
      default: undefined
    },
    baseClassName: {
      type: String,
      default: "checkbox"
    }
  },

  computed: {
    uniqId() {
      return this.id || `${this.baseClassName}-id-${uid()}`;
    }
  }
};



