<template>
  <Modal name="changeShape" :disable-backdrop="true">
    <Dialog
      name="changeShape"
      title="Удалить все отверстия?"
      :close="onClose"
    >
      <p>
        При смене формы детали, позиционировние отверстий будет утеряно.
        <br>
        Что будем делать?
      </p>

      <template slot="footer">
        <Button
          color="gray"
          size="m"
          theme="solid rounded"
          @click="onCancel"
        >
          Оставить отверстия
        </Button>
        &nbsp;
        <Button
          color="orange"
          size="m"
          theme="solid rounded"
          @click="onSuccess"
        >
          Удалить отверстия
        </Button>
      </template>
    </Dialog>
  </Modal>
</template>

<script>
import { Modal } from "vuex-modal";
import { Button, Dialog } from "@/components";

export default {
  name: "ConstructorChangeShapeTypeModal",

  components: {
    Modal,
    Dialog,
    Button
  },

  props: {
    onSuccess: {
      type: Function,
      default: () => {}
    },
    onCancel: {
      type: Function,
      default: () => {}
    },
    onClose: {
      type: Function,
      default: () => {}
    }
  }
};
</script>



