<template>

  <SVGPanZoom>
    <svg
      class="draw"
      xmlns="http://www.w3.org/2000/svg"
      :viewBox="`0 0 ${width} ${height}`"
      width="100%"
      height="100%"
      version="1.1"
    >
      <g class="draw__sketch">
        <Shape class="draw__shape" transform="translate(1, 1)" />
        <Dimensions class="draw__dimensions" />
      </g>
    </svg>
  </SVGPanZoom>

</template>

<script>
import { mapGetters, mapState } from "vuex";
import { SvgRoot, SVGPanZoom } from "@/components";
import Shape from "./Shape";
import Dimensions from "./Dimensions";

export default {
  name: "Draw",

  components: {
    SvgRoot,
    Dimensions,
    Shape,
    SVGPanZoom
  },

  computed: {
    ...mapState({
      shapeType: state => state.shape.type
    }),
    ...mapGetters({
      shape: "shape/shape"
    }),
    width() {
      switch (this.shapeType) {
        case "rectangle":
          return parseInt(this.shape.width, 10) + 50;

        case "circle":
          return parseInt(this.shape.diameter, 10);

        case "triangle":
          return parseInt(this.shape.side2, 10) + 50;

        default:
          return 0;
      }
    },
    height() {
      let height = 0;

      switch (this.shapeType) {
        case "rectangle":
          // eslint-disable-next-line prefer-destructuring
          height = this.shape.height;
          break;

        case "circle":
          height = this.shape.diameter;
          break;

        case "triangle":
          height = this.shape.side1;
          break;

        default:
          break;
      }

      return parseInt(height, 10) + 50; // dimension lines size
    }
  }
};
</script>



