<template>
  <g class="holes-list" v-if="isHaveHoles">
    <Hole
      class="hole-list__item"
      v-for="holeId in holes"
      :key="holeId"
      :id="holeId"
    />
  </g>
</template>

<script>
import { mapGetters } from "vuex";
import Hole from "./Hole";

export default {
  name: "DrawHolesList",

  components: {
    Hole
  },

  computed: {
    ...mapGetters({
      holes: "shape/getHolesAllIds",
      isHaveHoles: "shape/isHaveHoles"
    })
  }
};
</script>



