import cleanArray from "clean-array";
import Transformer from "./Transformer";

export default class CutMaterialTransformer extends Transformer {
  constructor(cutMaterials) {
    super();
    this.cutMaterials = cutMaterials;
  }

  getOptionsList(ids = []) {
    return cleanArray(
      ids.map(id => {
        const cutMaterial = this.cutMaterials[id];
        if (cutMaterial) {
          return {
            label: cutMaterial.name,
            value: cutMaterial.id
          };
        }

        return undefined;
      })
    );
  }

  getById(id) {
    return this.cutMaterials[id];
  }
}



