<template>
  <div :class="classes">
    <span class="visuallyhidden">Loading...</span>
  </div>
</template>

<script>
import ThemeClassesMixin from "@/mixins/themeClasses";

export default {
  name: "Spinner",

  mixins: [ThemeClassesMixin],

  props: {
    size: {
      type: String,
      default: "medium"
    },
    color: {
      type: String,
      default: "blue"
    },
    theme: {
      type: [String, Array],
      default: "1"
    },
    baseClassName: {
      type: String,
      default: "spinner"
    }
  },

  computed: {
    classes() {
      return [
        this.baseClassName,
        {
          [`${this.baseClassName}_color_${this.color}`]: this.color,
          [`${this.baseClassName}_size_${this.size}`]: this.size
        },
        ...this.themeClasses
      ];
    }
  }
};
</script>


<style lang="stylus">
.spinner
  &_style_1
    border-style solid
    border-right-color transparent !important

  &_style_2
    border-style solid
    border-color "rgba(currentColor, 0.25)"
    border-top-color currentColor

  &_inherit
    overflow: visible;
    font: inherit;
    color: inherit;
    text-transform: none;
    padding: 0;
    background-color: transparent;
    display: inline-block;
    fill: currentColor;
    line-height: 0;
    border-color: currentColor;
</style>



